import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import FancyRoute from "./components/fancyRoute"

import Intro from "./pages/intro";
import About from "./pages/about";
import Experience from "./pages/experience";
import Artwork from "./pages/artwork";
import Moviebay from "./pages/artwork/moviebay";
import Mncland from "./pages/artwork/mncland";
import Kayazta from "./pages/artwork/kayazta";
import Alifbata from "./pages/artwork/alifbata";

const routes = [
  {
    path: '/',
    exact: true,
    component: Intro
  },
  {
    path: '/experience',
    component: Experience,
    exact: true
  },
  {
    path: '/about',
    component: About,
    exact: true
  },
  {
    path: '/artwork',
    component: Artwork,
    exact: true
  },
  {
    path: '/artwork/moviebay',
    component: Moviebay,
    exact: true
  },
  {
    path: '/artwork/mncland',
    component: Mncland,
    exact: true
  },
  {
    path: '/artwork/kayazta',
    component: Kayazta,
    exact: true
  },
  {
    path: '/artwork/alifbata',
    component: Alifbata,
    exact: true
  }
]

function App() {
  return (
    <Router>
      <Switch>
        {routes.map((route, i) =>
          <FancyRoute key={i} {...route} />
        )}
      </Switch>
    </Router>
  );
}

export default App;
